// CSS
import 'leaflet/dist/leaflet.css';
import 'leaflet-draw/dist/leaflet.draw.css';

// JS
import L from 'leaflet';
import 'leaflet-draw';
import '@/global/widgets/color-picker/TColorPicker';
// Note: If markers are ever used in GeoFence Editor/Viewer, then see image fix in leaflet-timetrex. https://github.com/Leaflet/Leaflet/issues/4968

export class GEOFenceViewController extends BaseViewController {
	constructor( options = {} ) {
		_.defaults( options, {
			el: '#geo_fence_view_container',

			// _required_files: {
			// 	15: ['leaflet-timetrex', 'TColorPicker']
			// },
			type_array: null,

			company_api: null,
			drawControl: null,
			bounds: null,
			drawnItems: null,
			search_key_delay: 0,
			start_coordinates: null,
			map_html_id: null
		} );

		super( options );
	}

	init() {
		//this._super('initialize' );
		this.edit_view_tpl = 'GEOFenceEditView.html';
		this.permission_id = 'geo_fence';
		this.viewId = 'GEOFence';
		this.script_name = 'GEOFenceView';
		this.table_name_key = 'geo_fence';
		this.context_menu_name = $.i18n._( 'GEO Fence' );
		this.navigation_label = $.i18n._( 'GEO Fence' ) + ':';
		this.api = TTAPI.APIGEOFence;
		this.company_api = TTAPI.APICompany;

		if ( this.map ) {
			delete ( this.map );
		}

		this.render();
		this.buildContextMenu();
		this.initData();
		this.setSelectRibbonMenuIfNecessary();
	}

	getCustomContextMenuModel() {
		var context_menu_model = {
			groups: {
				map: {
					label: $.i18n._( 'Map' ),
					id: this.viewId + 'Navigation'
				}
			},
			exclude: [ContextMenuIconName.export_excel, ContextMenuIconName.mass_edit],
			include: [{
				label: $.i18n._( 'Map' ),
				id: ContextMenuIconName.map,
				group: 'map',
				icon: Icons.map
			}]
		};

		return context_menu_model;
	}

	onCustomContextClick( id ) {
		switch ( id ) {
			case ContextMenuIconName.map:
				this.onMapClick();
				break;
		}
	}

	onSaveAndContinueClick() {
		super.onSaveAndContinueClick();
	}

	onMapClick() {
		// only trigger map load in specific product editions.
		if ( ( Global.getProductEdition() >= 15 ) ) {
			var geo_fences;
			if ( this.edit_view ) {
				geo_fences = [this.current_edit_record];
			} else if ( this.getGridSelectIdArray().length > 0 ) {
				geo_fences = this.getSelectedItems();
			} else {
				geo_fences = this.grid.getGridParam( 'data' );
			}
			// Currently not doing pre-processing for geofences, handling this directly in MapViewController
			// var processed_data_for_map = TTMapLib.TTConvertMapData.processBasicFromGenericViewController(geo_fences);
			IndexViewController.openEditView( this, 'Map', geo_fences );
		}
	}

	onAddClick() {
		$( '.GEOFenceEditView' ).remove();

		TTPromise.add( 'init', 'init' );
		super.onAddClick();

		var $this = this;
		TTPromise.wait( 'init', 'init', function() {

			// Clear the old record to prevent duplication on inserting an empty geofence record. See #2131
			$this._selectedShape = false;
			$this.current_edit_record = {};
			for ( var key in $this.edit_view_ui_dic ) {
				$this.edit_view_ui_dic[key].setValue( '' );
			}

			if ( !$this._map ) {
				$this._initLeafletMap();
			}

			$this._map.setView( $this.start_coordinates, 10 );
		} );
	}

	getFilterColumnsFromDisplayColumns() {
		var column_filter = {};
		column_filter.is_owner = true;
		column_filter.id = true;
		column_filter.is_child = true;
		column_filter.in_use = true;
		column_filter.first_name = true;
		column_filter.last_name = true;
		column_filter.geo_circle = true;
		column_filter.geo_color = true;
		column_filter.geo_polygon = true;
		column_filter.geo_type_id = true;

		// Error: Unable to get property 'getGridParam' of undefined or null reference
		var display_columns = [];
		if ( this.grid ) {
			display_columns = this.grid.getGridParam( 'colModel' );
		}
		//Fixed possible exception -- Error: Unable to get property 'length' of undefined or null reference in /interface/html5/views/BaseViewController.js?v=7.4.3-20140924-090129 line 5031
		if ( display_columns ) {
			var len = display_columns.length;

			for ( var i = 0; i < len; i++ ) {
				var column_info = display_columns[i];
				column_filter[column_info.name] = true;
			}
		}

		return column_filter;
	}

	buildEditViewUI() {

		super.buildEditViewUI();
		var $this = this;

		var tab_model = {
			'tab_geo_fence': { 'label': $.i18n._( 'GEO Fence' ) },
			'tab_audit': true,
		};
		this.setTabModel( tab_model );

		var form_item_input;

		this.navigation.AComboBox( {
			api_class: TTAPI.APIGEOFence,
			id: this.script_name + '_navigation',
			allow_multiple_selection: false,
			layout_name: 'global_geo_fence',
			navigation_mode: true,
			show_search_inputs: true
		} );

		this.setNavigation();

		//Tab 0 start

		var tab_geo_fence = this.edit_view_tab.find( '#tab_geo_fence' );

		var tab_geo_fence_column1 = tab_geo_fence.find( '.first-column' );

		this.edit_view_tabs[0] = [];

		this.edit_view_tabs[0].push( tab_geo_fence_column1 );

		// Name

		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );

		form_item_input.TTextInput( { field: 'name', width: '100%' } );
		this.addEditFieldToColumn( $.i18n._( 'Name' ), form_item_input, tab_geo_fence_column1 );
		form_item_input.parent().width( '45%' );

		//Color
		form_item_input = Global.loadWidgetByName( FormItemType.COLOR_PICKER );
		form_item_input.TColorPicker( { field: 'geo_color' } );
		this.addEditFieldToColumn( $.i18n._( 'Color' ), form_item_input, tab_geo_fence_column1 );

		// Description
		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );

		form_item_input.TTextInput( { field: 'description', width: '100%' } );
		this.addEditFieldToColumn( $.i18n._( 'Description' ), form_item_input, tab_geo_fence_column1 );

		form_item_input.parent().width( '45%' );

		this._initLeafletMap();
	}

	_initLeafletMap() {
		this.neeed_save_punches_dic = {};
		this.layers = [];
		this.line_layers = [];
		this.circle_layers = [];
		this.directions_displays = [];
		this.info_panel_dic = {};
		this.geo_layers = [];
		//init map

		if ( !this._map || this._map == undefined || $( '#tab_geo_fence:visible #map_container div.google-map' )[0] == undefined ) {
			//Avoid map is already initialized js exception by dynamically attaching the map element.
			// if (this.map_html_id && $('#' + this.map_html_id).length > 0) {
			// 	$( '#tab_geo_fence:visible #map_container #' + this.map_html_id ).off();
			// 	Debug.Text('Removed Lmap id: ' + this.map_html_id, 'GEOFenceViewController.js', 'GEOFenceViewController', '_initLeafletMap', 10);
			// }

			this.map_html_id = 'map_' + ( new Date() ).getTime();
			Debug.Text( 'New Lmap id: ' + this.map_html_id, 'GEOFenceViewController.js', 'GEOFenceViewController', '_initLeafletMap', 10 );

			//append the map element, use then to ensure it's complete before initiallizing the map.
			$( '#tab_geo_fence:visible #map_container' ).html( '<div id="' + this.map_html_id + '" class="google-map"></div>' );
			this._map = L.map( this.map_html_id );

			Debug.Arr( this._map, 'Lmap var', 'GEOFenceViewController.js', 'GEOFenceViewController', '_initLeafletMap', 10 );
		}

		var osm;
		if ( APIGlobal.pre_login_data.map_provider == 'timetrex' ) {
			var osmUrl = APIGlobal.pre_login_data.map_tile_url + '/{z}/{x}/{y}.png?tt_key=' + APIGlobal.pre_login_data.registration_key;
			var osmAttrib = 'Map data by Â©<a href="http://www.openstreetmap.org/copyright">OpenStreetMap</a>.';
			osm = new L.TileLayer( osmUrl, { minZoom: 3, maxZoom: 18, attribution: osmAttrib, noWrap: true } );
		} else {
			osm = L.tileLayer.provider( APIGlobal.pre_login_data.map_provider );
		}

		osm.on( 'load', function() {
			TTPromise.resolve( 'Map', 'render' );
		} );

		this._map.addLayer( osm );

		this.start_coordinates = this.startMapCoordinates();

		this._map.setView( this.start_coordinates, 10 );

		this.drawnItems = new L.FeatureGroup();
		this.drawnItems.do_not_clear = true;
		this._map.addLayer( this.drawnItems );

		this.drawControl = new L.Control.Draw( {
			edit: {
				featureGroup: this.drawnItems,
				edit: false,
				remove: false
			},
			draw: {
				polygon: {
					shapeOptions: { color: '#0F820F' }
				},
				circle: {
					shapeOptions: { color: '#0F820F' }
				},
				polyline: false,
				rectangle: false,
				marker: false,
				circlemarker: false
			}
		} );

		this._map.addControl( this.drawControl );

		var $this = this;
		this._map.on( L.Draw.Event.DRAWSTART, function( e ) {
			$this.removeAllLayers();
		} );

		this._map.on( L.Draw.Event.CREATED, function( e ) {
			var type = e.layerType,
				new_layer = e.layer;
			new_layer.editable = true;
			new_layer.removable = true;

			$this.removeAllLayers();
			$this._selectedShape = new_layer;
			$this.drawnItems.addLayer( new_layer );
			new_layer.editing.enable();
			$this.is_changed = true;
			$this.setEditMenu();
			$this.validate();
		} );

		this._initSearchBox();
	}

	_onShapeUpdate( e ) {
		var edit_layer = e.target;
		edit_layer.editable = true;
		edit_layer.removable = true;
		this._selectedShape = edit_layer;
		this.drawnItems.addLayer( edit_layer );
		edit_layer.editing.enable();
		this.is_changed = true;
		this.setEditMenu();
	}

	removeAllLayers() {
		for ( var layer in this.drawnItems._layers ) {
			if ( this.drawnItems._layers[layer].removable == true ) {
				try { //try needed for intermittent low-level non-blocking error when users navigate faster than leaflet can draw
					this.drawnItems.removeLayer( this.drawnItems._layers[layer] );
				} catch ( e ) {
					Debug.Text( e.message, 'GeoFenceController.js', 'GeoFenceController', 'GeoFenceController', 10 );
				}
			}
		}
	}

	onFormItemChange( target, doNotValidate ) {
		super.onFormItemChange( target, doNotValidate );
		var key = target.getField();
		if ( key == 'geo_color' ) {
			this.drawControl.options.draw.polygon.shapeOptions.color = this.edit_view_ui_dic[key].val();
			this.drawControl.options.draw.circle.shapeOptions.color = this.edit_view_ui_dic[key].val();

			for ( var layer in this.drawnItems._layers ) {
				if ( this.drawnItems._layers[layer].is_search_marker === undefined ) { // Only change the colours for layers that are not a search marker. Otherwise this will result in an error when changing colours after a search.
					this.drawnItems._layers[layer].setStyle( {
						fillColor: this.edit_view_ui_dic[key].val(),
						color: this.edit_view_ui_dic[key].val()
					} );
				}
			}
		}
	}

	_initSearchBox() {
		var $this = this;
		$( '#pac-input' ).bind( 'focus', function( e ) {
			$( this ).select();
		} );

		$( '#pac-input' ).bind( 'keyup', function( e ) {
			if ( e.keyCode == 13 ) {
				$( '#suggestion-box div' ).first().click();
			} else {
				$this.search_key_delay = parseInt( Date.now() );
				window.setTimeout( function() {
					if ( parseInt( Date.now() ) - $this.search_key_delay >= 250 ) {
						$this.stop_suggestions = false;
						$this.searchSuggest();
					}
				}, 300 );
			}
		} );
	}

	searchSuggest() {
		$( '#suggestion-box' ).html( '' );
		var $this = this;
		if ( $( '#pac-input' ).val().length >= 3 ) {
			$( '#pac-input' ).css( 'border', '1px dotted green' );

			var url = APIGlobal.pre_login_data.map_geocode_url + '?q=' + encodeURIComponent( $( '#pac-input' ).val() ) + '&format=json&tt_key=' + APIGlobal.pre_login_data.registration_key;

			$.get( url, function( data ) {
				$( '#pac-input' ).css( 'border', 'none' );
				if ( $this.stop_suggestions == false ) {
					$( '#suggestion-box' ).html( '' );
					for ( var n in data ) {
						var name = data[n].display_name;
						var lat = data[n].lat;
						var lng = data[n].lon;
						var suggest = '<div class=\'map-suggest\' lat=\'' + lat + '\' lng=\'' + lng + '\' bound0=\'' + data[n].boundingbox[0] + '\' bound1=\'' + data[n].boundingbox[1] + '\' bound2=\'' + data[n].boundingbox[2] + '\' bound3=\'' + data[n].boundingbox[3] + '\' style=\'display:block;position:relative;cursor:pointer;padding:7px;background:white;\'>' + name + '</div>';
						$( '#suggestion-box' ).append( suggest );
						$( '#suggestion-box .map-suggest' ).bind( 'click', function() {
							$this._map.panTo( new L.LatLng( $( this ).attr( 'lat' ), $( this ).attr( 'lng' ) ) );
							var address = $( this ).html();
							var bound00 = $( this ).attr( 'bound0' );
							var bound01 = $( this ).attr( 'bound2' );
							var bound10 = $( this ).attr( 'bound1' );
							var bound11 = $( this ).attr( 'bound3' );

							$( '#pac-input' ).val( address );
							var bounds = new L.latLngBounds( [bound00, bound01], [bound10, bound11] );
							$this._map.fitBounds( bounds, { padding: [20, 20] } );
							//$this._map.panTo(bounds.getCenter());

							var greenIcon = L.icon( {
								iconUrl: 'framework/leaflet/images/marker-icon-red.png',
								shadowUrl: 'framework/leaflet/images/marker-shadow.png',

								iconSize: [25, 41], // size of the icon
								shadowSize: [41, 41], // size of the shadow
								iconAnchor: [0, 0], // point of the icon which will correspond to marker's location
								shadowAnchor: [0, 0],  // the same for the shadow
								popupAnchor: [12, 0] // point from which the popup should open relative to the iconAnchor
							} );

							for ( var layer in $this.drawnItems._layers ) {
								if ( $this.drawnItems._layers[layer].is_search_marker != undefined && $this.drawnItems._layers[layer].is_search_marker == true ) {
									$this.drawnItems.removeLayer( $this.drawnItems._layers[layer] );
								}
							}

							var marker = L.marker( bounds.getCenter(), {
								draggable: false,
								icon: greenIcon
							} );
							marker.is_marker = true;
							marker.is_search_marker = true;

							marker.addTo( $this.drawnItems ).bindPopup( address ).openPopup();

							$( '#suggestion-box' ).html( '' );
							$( '#suggestion-box' ).fadeOut( 'fast' );
							$this.stop_suggestions = true;
						} );
						$( '#suggestion-box' ).fadeIn( 'fast' );
					}
				}
			} );
		}
	}

	saveLayout() {
		var $this = this;
		this.user_generic_data_api.setUserGenericData( this.select_layout, {
			onResult: function( result ) {
				if ( result.isValid() ) {
					var result_data = result.getResult();
					if ( TTUUID.isUUID( result_data ) && result_data != TTUUID.zero_id && result_data != TTUUID.not_exist_id ) {
						$this.select_layout.id = result_data;
					}
				}
			}
		} );
	}

	centerMap() {
		this._map.panTo( this.start_coordinates );
	}

	uniformVariable( record ) {
		record.geo_circle = false;
		record.geo_type_id = 10; // default to circle prevent "Incorrect GEO type" exception triggered.
		record.map_level = false;
		if ( this._selectedShape && this._selectedShape._radius != undefined ) {
			record.geo_circle = {};
			record.geo_circle.center = [this._selectedShape.getLatLng().lat, this._selectedShape.getLatLng().lng];
			record.geo_circle.radius = this._selectedShape._mRadius;
			record.geo_type_id = 20;
			record.map_level = this._map.getZoom();
		} else if ( this._selectedShape ) {
			var latlngs = this._selectedShape.getLatLngs()[0]; //getPaths().getAt( 0 ).getArray();
			var array = [];
			for ( var i = 0; i < latlngs.length; i++ ) {
				array.push( [latlngs[i].lat, latlngs[i].lng] );
			}
			record.geo_polygon = array;
			record.geo_type_id = 10;
			record.map_level = this._map.getZoom();
		}
		return record;
	}

	setCurrentEditRecordData() {
		var $this = this;
		this.user_generic_data_api.getUserGenericData( { filter_data: { script: 'MapView', deleted: false } }, {
			onResult: function( results ) {
				var result_data = results.getResult();
				if ( result_data && result_data.length > 0 ) {
					$this.select_layout = result_data[0];
				} else {
					$this.select_layout = {
						script: 'MapView',
						is_default: false,
						name: Global.default_item,
						data: {
							marker_connection_type: 'LINE'
						}
					};
				}
				//Set current edit record data to all widgets
				//$this._selectedShape && $this._deleteSelectShape();
				for ( var key in $this.current_edit_record ) {
					if ( !$this.current_edit_record.hasOwnProperty( key ) ) {
						continue;
					}
					var widget = $this.edit_view_ui_dic[key];
					if ( Global.isSet( widget ) ) {
						switch ( key ) {
							default:
								widget.setValue( $this.current_edit_record[key] );
								break;
						}
					}
				}

				if ( $this.drawnItems ) {
					$this.removeAllLayers();
				}

				$this.bounds = new L.LatLngBounds();
				if ( $this.current_edit_record.geo_type_id == 20 && $this.current_edit_record.geo_circle ) {
					$this._drawCircle( $this.current_edit_record.geo_circle );
				} else if ( $this.current_edit_record.geo_type_id == 10 && $this.current_edit_record.geo_polygon ) {
					$this._drawPolygon( $this.current_edit_record.geo_polygon );
				} else {
					$this.centerMap();
				}

				$this.current_edit_record.map_level = $this._map.getZoom();

				$this.collectUIDataToCurrentEditRecord();
				$this.setEditViewDataDone();
			}
		} );
	}

	_drawCircle( geo_circle ) {
		Debug.Text( 'drawing circle', 'GEOFenceViewController.js', 'GEOFenceViewController', '_drawCircle', 10 );
		var layer = new L.Circle( [parseFloat( geo_circle.center[0] ), parseFloat( geo_circle.center[1] )], parseFloat( geo_circle.radius ), {
			stroke: true,
			fill: true,
			opacity: 0.8,
			weight: 2,
			color: this.current_edit_record.geo_color ? '#' + this.current_edit_record.geo_color : '#0F820F',
			fillOpacity: 0.25,
			fillColor: this.current_edit_record.geo_color ? '#' + this.current_edit_record.geo_color : '#0F820F'
		} );
		layer.removable = true;
		layer.addTo( this.drawnItems );//.bindPopup(this.current_edit_record.name,{closeButton:false, className:'polypopup'}).openPopup();

		if ( this.is_edit ) {
			layer.editing.enable();
		} else {
			layer.editing.disable();
		}

		this._map.fitBounds( layer.getBounds(), { padding: [20, 20] } );
		this.geo_layers.push( layer );
		var $this = this;

		layer.on( 'edit', function( e ) {
			$this._onShapeUpdate( e );
		} );
	}

	_drawPolygon( triangleCoords ) {
		Debug.Text( 'drawing polygon', 'GEOFenceViewController.js', 'GEOFenceViewController', '_drawPolygon', 10 );
		var latlngs = _.map( triangleCoords, function( item ) {
			return { lat: parseFloat( item[0] ), lng: parseFloat( item[1] ) };
		} );

		var layer = new L.Polygon( triangleCoords, {
			stroke: true,
			fill: true,
			opacity: 0.8,
			weight: 2,
			color: this.current_edit_record.geo_color ? '#' + this.current_edit_record.geo_color : '#0F820F',
			fillOpacity: 0.25,
			fillColor: this.current_edit_record.geo_color ? '#' + this.current_edit_record.geo_color : '#0F820F'
		} );
		layer.removable = true;

		if ( this.is_edit ) {
			layer.editing.enable();
		} else {
			layer.editing.disable();
		}

		layer.addTo( this.drawnItems );//.bindPopup(this.current_edit_record.name,{closeButton:false, className:'polypopup'}).openPopup();

		this._map.fitBounds( layer.getBounds(), { padding: [20, 20] } );
		this.geo_layers.push( layer );
		var $this = this;

		layer.on( 'edit', function( e ) {
			$this._onShapeUpdate( e );
		} );
	}

	buildSearchFields() {

		super.buildSearchFields();
		this.search_fields = [

			new SearchField( {
				label: $.i18n._( 'Name' ),
				in_column: 1,
				field: 'name',
				multiple: true,
				basic_search: true,
				form_item_type: FormItemType.TEXT_INPUT
			} ),

			new SearchField( {
				label: $.i18n._( 'Created By' ),
				in_column: 2,
				field: 'created_by',
				layout_name: 'global_user',
				api_class: TTAPI.APIUser,
				multiple: true,
				basic_search: true,
				script_name: 'EmployeeView',
				form_item_type: FormItemType.AWESOME_BOX
			} ),

			new SearchField( {
				label: $.i18n._( 'Updated By' ),
				in_column: 2,
				field: 'updated_by',
				layout_name: 'global_user',
				api_class: TTAPI.APIUser,
				multiple: true,
				basic_search: true,
				script_name: 'EmployeeView',
				form_item_type: FormItemType.AWESOME_BOX
			} )

		];
	}

	setEditMenuMapIcon( context_btn ) {

		if ( Global.getProductEdition() <= 10 ) {
			context_btn.addClass( 'invisible-image' );
		}
		context_btn.addClass( 'disable-image' );
	}

	setDefaultMenuMapIcon( context_btn, grid_selected_length ) {
		if ( Global.getProductEdition() <= 10 ) {
			context_btn.addClass( 'invisible-image' );
		}

		if ( grid_selected_length > 0 ) {
			context_btn.removeClass( 'disable-image' );
		} else {
			context_btn.addClass( 'disable-image' );
		}
	}

	setEditViewDataDone() {
		this.is_edit = true;
		super.setEditViewDataDone();
		var $this = this;
		TTPromise.wait( null, null, function() {
			//set the color of the polygon at the start
			if ( $this.edit_view_ui_dic.geo_color ) {
				$this.drawControl.options.draw.polygon.shapeOptions.color = $this.edit_view_ui_dic.geo_color.val();
				$this.drawControl.options.draw.circle.shapeOptions.color = $this.edit_view_ui_dic.geo_color.val();
			}

		} );
	}

	onSaveAndContinue( ignoreWarning ) {
		this.removeAllLayers();
		super.onSaveAndContinue();
	}

	setNavigation() {

		var $this = this;

		//Error: Unable to get value of the property 'getGridParam': object is null or undefined in /interface/html5/views/BaseViewController.js?v=8.0.0-20141230-103725 line 2575
		if ( !this.grid ) {
			return;
		}

		this.navigation.setPossibleDisplayColumns( this.buildDisplayColumnsByColumnModel( this.grid.grid.getGridParam( 'colModel' ) ),
			this.buildDisplayColumns( this.default_display_columns ) );

		this.navigation.unbind( 'formItemChange' ).bind( 'formItemChange', function( e, target ) {

			var key = target.getField();
			var next_select_item_id = target.getValue();

			if ( !next_select_item_id ) {
				return;
			}

			if ( next_select_item_id !== $this.current_edit_record.id ) {
				ProgressBar.showOverlay();
				TTPromise.add( 'Map', 'render' );
				if ( $this.is_viewing ) {
					$this.onViewClick( next_select_item_id ); //Dont refresh UI
				} else {
					$this.onEditClick( next_select_item_id ); //Dont refresh UI
				}

			}
			//wait for map to render and prevent js exception from moving too fast.
			TTPromise.wait( 'Map', 'render', function() {
				$this.setNavigationArrowsEnabled();
			} );

		} );

	}
}